@import url(https://fonts.googleapis.com/css2?family=Noticia+Text&display=swap);
.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.main-heading {
    font-size: 2.5em;
    margin-bottom: 2.5%;
}
.post-container{
    background-color: #e8e3d5;
    display: flex;
    flex-direction: column;
    padding: 2%;
    margin: 0 1%;
    font-family: 'Noticia Text', serif;
}

.heading{
    height: 125px;
    text-align: center;
    display: flex;
    align-items: center;
}

.image{
    width: 100%;
    height: 200px;
}

.info{
    display: flex;
    justify-content: sp;
}
#root {
    height: 100vh;
}

body {
    overflow: hidden;
    background-color: #C3BCAA;
}

.posts-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
